<h1 id="群机器人" tabindex="-1"><a class="header-anchor" href="#群机器人" aria-hidden="true">#</a> 群机器人</h1>
<h2 id="使用说明" tabindex="-1"><a class="header-anchor" href="#使用说明" aria-hidden="true">#</a> 使用说明</h2>
<p>使用前必须先在群组里面添加机器人，然后将 <code>Webhook 地址</code> 中的 <code>key</code> 取出来，作为示例中 <code>$groupKey</code> 的值。</p>
<blockquote>
<p>Webhook 地址示例：https://qyapi.weixin.qq.com/cgi-bin/webhook/send?key=<code>ab4f609a-3feb-427c-ae9d-b319ca712d36</code></p>
</blockquote>
<blockquote>
<p>微信文档：https://work.weixin.qq.com/api/doc#90000/90136/91770</p>
</blockquote>
<h2 id="发送文本类型消息" tabindex="-1"><a class="header-anchor" href="#发送文本类型消息" aria-hidden="true">#</a> 发送文本类型消息</h2>
<p>快速发送文本消息</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token comment">// 获取 Messenger 实例</span>
<span class="token variable">$messenger</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">group_robot_messenger</span><span class="token punctuation">;</span>

<span class="token comment">// 群组 key</span>
<span class="token variable">$groupKey</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'ab4f609a-3feb-427c-ae9d-b319ca712d36'</span><span class="token punctuation">;</span>

<span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'大家好，我是本群的"喝水提醒小助手"'</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toGroup</span><span class="token punctuation">(</span><span class="token variable">$groupKey</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 或者写成</span>
<span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">toGroup</span><span class="token punctuation">(</span><span class="token variable">$groupKey</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'大家好，我是本群的"喝水提醒小助手"'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div><p>使用 <code>Text</code> 发送文本消息</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Work<span class="token punctuation">\</span>GroupRobot<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>Text</span><span class="token punctuation">;</span>

<span class="token comment">// 准备消息</span>
<span class="token variable">$text</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Text</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'hello'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token comment">// 发送</span>
<span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token variable">$text</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toGroup</span><span class="token punctuation">(</span><span class="token variable">$groupKey</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><p>@某人：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Work<span class="token punctuation">\</span>GroupRobot<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>Text</span><span class="token punctuation">;</span>

<span class="token comment">// 通过构造函数传参</span>
<span class="token variable">$text</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Text</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'hello'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'her-cat'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'18700000000'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">//$text = new Text('hello', ['her-cat', 'overtrue'], ['18700000000', '18700000001']);</span>

<span class="token comment">// 通过 userId</span>
<span class="token variable">$text</span><span class="token operator">-></span><span class="token function">mention</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'her-cat'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">//$text->mention(['her-cat', 'overtrue']);</span>

<span class="token comment">// 通过手机号</span>
<span class="token variable">$text</span><span class="token operator">-></span><span class="token function">mentionByMobile</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'18700000000'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">//$text->mentionByMobile(['18700000000', '18700000001']);</span>

<span class="token comment">// @所有人</span>
<span class="token variable">$text</span><span class="token operator">-></span><span class="token function">mention</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'@all'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">//$text->mentionByMobile('@all');</span>

<span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token variable">$text</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toGroup</span><span class="token punctuation">(</span><span class="token variable">$groupKey</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div><h2 id="发送-markdown-类型消息" tabindex="-1"><a class="header-anchor" href="#发送-markdown-类型消息" aria-hidden="true">#</a> 发送 Markdown 类型消息</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Work<span class="token punctuation">\</span>GroupRobot<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>Markdown</span><span class="token punctuation">;</span>

<span class="token variable">$content</span> <span class="token operator">=</span> <span class="token string single-quoted-string">'
# 标题一
## 标题二
&lt;font color="info">绿色&lt;/font>
&lt;font color="comment">灰色&lt;/font>
&lt;font color="warning">橙红色&lt;/font>
> 引用文字
'</span><span class="token punctuation">;</span>

<span class="token variable">$markdown</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Markdown</span><span class="token punctuation">(</span><span class="token variable">$content</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token variable">$markdown</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toGroup</span><span class="token punctuation">(</span><span class="token variable">$groupKey</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br></div></div><h2 id="发送图片类型消息" tabindex="-1"><a class="header-anchor" href="#发送图片类型消息" aria-hidden="true">#</a> 发送图片类型消息</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Work<span class="token punctuation">\</span>GroupRobot<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>Image</span><span class="token punctuation">;</span>

<span class="token variable">$img</span> <span class="token operator">=</span> <span class="token function">file_get_contents</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'http://res.mail.qq.com/node/ww/wwopenmng/images/independent/doc/test_pic_msg1.png'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$image</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">Image</span><span class="token punctuation">(</span><span class="token function">base64_encode</span><span class="token punctuation">(</span><span class="token variable">$img</span><span class="token punctuation">)</span><span class="token punctuation">,</span> <span class="token function">md5</span><span class="token punctuation">(</span><span class="token variable">$img</span><span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token variable">$image</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toGroup</span><span class="token punctuation">(</span><span class="token variable">$groupKey</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br></div></div><h2 id="发送图文类型消息" tabindex="-1"><a class="header-anchor" href="#发送图文类型消息" aria-hidden="true">#</a> 发送图文类型消息</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Work<span class="token punctuation">\</span>GroupRobot<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>News</span><span class="token punctuation">;</span>
<span class="token keyword">use</span> <span class="token package">EasyWeChat<span class="token punctuation">\</span>Work<span class="token punctuation">\</span>GroupRobot<span class="token punctuation">\</span>Messages<span class="token punctuation">\</span>NewsItem</span><span class="token punctuation">;</span>

<span class="token variable">$items</span> <span class="token operator">=</span> <span class="token punctuation">[</span>
    <span class="token keyword">new</span> <span class="token class-name">NewsItem</span><span class="token punctuation">(</span><span class="token punctuation">[</span>
        <span class="token string single-quoted-string">'title'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'中秋节礼品领取'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'description'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'今年中秋节公司有豪礼相送'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'url'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'https://www.easywechat.com'</span><span class="token punctuation">,</span>
        <span class="token string single-quoted-string">'image'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'http://res.mail.qq.com/node/ww/wwopenmng/images/independent/doc/test_pic_msg1.png'</span><span class="token punctuation">,</span>
    <span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">,</span>

    <span class="token comment">//...</span>
<span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token variable">$news</span> <span class="token operator">=</span> <span class="token keyword">new</span> <span class="token class-name">News</span><span class="token punctuation">(</span><span class="token variable">$items</span><span class="token punctuation">)</span><span class="token punctuation">;</span>

<span class="token variable">$messenger</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token variable">$news</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toGroup</span><span class="token punctuation">(</span><span class="token variable">$groupKey</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br></div></div><h2 id="其他方式" tabindex="-1"><a class="header-anchor" href="#其他方式" aria-hidden="true">#</a> 其他方式</h2>
<p>使用 <code>group_robot</code> 发送消息。</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">group_robot</span><span class="token operator">-></span><span class="token function">message</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'大家好，我是本群的"喝水提醒小助手"'</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">toGroup</span><span class="token punctuation">(</span><span class="token variable">$groupKey</span><span class="token punctuation">)</span><span class="token operator">-></span><span class="token function">send</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>